@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Poppins", source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
  min-width: 320px;
}

* {
  font-family: "Poppins";
  font-size: 16px;
}

a:hover {
  color: #77BD75;
}

a {
  color: #768761;
}

div {
  color: white;
}

span.anticon.anticon-info-circle svg {
  width: 1.4rem;
  height: 1.4rem;
}

span.anticon.anticon-robot svg {
  width: 2rem;
  height: 2rem;
}

#nav-root {
  background-color: lightgray;
}

#nav-menu {
  background-color: #77BD75;
  padding: 4px 0 4px 0;
}

.logo {
  height: 48px;
  margin: 16px;
}

.content {
  width: calc(100% - 2rem);
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 16px 16px 0px 16px;
  background-color: #454545;
  color: white;
}

.ant-layout-sider-trigger {
  background-color: #768761;
}

.ant-menu-vertical .ant-menu-item::after, .ant-menu-vertical-left .ant-menu-item::after, .ant-menu-vertical-right .ant-menu-item::after, .ant-menu-inline .ant-menu-item::after {
  border-right: 3px solid #768761;
}

.ant-menu-item-selected a:hover {
  color: #768761 !important;
}

.ant-menu-item a {
  color:white !important;
}

.ant-menu-item a:hover {
  color: #768761 !important;
}

.ant-menu-item-selected a {
  color: #768761 !important;
}

.default-button, .default-button:hover, .default-button:focus {
  border: none;
  background-color: #77BD75;
  color: whitesmoke;
}

#shrunk-nav-root {
  margin: 2rem 2rem 1rem 2rem;
  align-self: flex-start;
  display: none;
  float: right;
}

.shrunk-link-background {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #2b2b2b;
}

@media only screen and (max-width: 768px) {

  #nav-root {
    display: none;
  }

  #shrunk-nav-root {
    display: flex;
    align-self: center;
    justify-content: center;
    flex-direction: row;
    
    min-height: 2.6rem;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    border-radius: 8px;
    padding: 2rem 2rem 0rem 2rem;
  }

  .content {
    width: 100%;
    margin: 0px 0px 0px 0px;
  }

  .anticon-mail svg, .anticon-user svg, .anticon-sliders svg {
    font-size: 1.2rem;
  }

} 





#profile-root {
    padding: 2rem 2rem 2rem 2rem;
    width: 100%;
    height: 100%;
}

#profile-img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
}  

#profile-welcome-text {
    font-size: 2rem;
    font-weight: 600;
    color: #768761;
}

#profile-content-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin-top: 10%;
    margin-bottom: 10%;
}

.login-button {
    border: none;
    background-color: #77BD75;
    color: whitesmoke;
    width: 120px;
}

@media only screen and (max-width: 768px) {

    #profile-img {
        width: 140px;
        height: 140px;
    }  

}
#contact-root {
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5% 10% 10% 10%;
    height: 100%;
    grid-gap: 2rem;
    gap: 2rem;
}

#contact-input-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

#contact-input {
    font-size: 1.3rem;
}

.contact-text {
    color: #beddbe;
    font-size: 2rem;
}

@media only screen and (max-width: 768px) {

    .contact-text {
        color: #beddbe;
        font-size: 1.5rem;
    }

} 
#home-root {
    padding: 8% 8% 8% 8%;
    min-height: 100%;
}

#top-songs-info-box {
    display: flex;
    flex-direction: column;
}

#top-songs-lists {
    display: flex;
    flex-direction: row;
    height: 26rem;
    width: 100%;
    grid-gap: 4rem;
    gap: 4rem;
    justify-content: center;
}

#playlists-box {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.playlist-preview-img {
    background-size: 15rem !important;
    width: 15rem;
    height: 15rem;
    border: 0.1rem solid white;
    cursor: pointer;
    overflow: auto;
}

@media only screen and (max-width: 768px) {

    #top-songs-lists {
        flex-direction: column;
        justify-content: center;
        width: 100%;
        grid-gap: 2rem;
        gap: 2rem;
        height: 54rem
    }

}

@media only screen and (max-width: 400px) {

    .playlist-img {
        width: 10rem;
        height: 10rem;
        background-size: 10rem !important;
    }

    #playlists-box {
        grid-gap: 1rem;
        gap: 1rem;
    }

}

@media only screen and (max-width: 610px) {

    .playlist-img {
        width: 7rem;
        height: 7rem;
        background-size: 7rem 7rem !important;
    }
} 
#track-list-root {
    width: 46%;
    height: 26rem;
    display: flex;
    flex-direction: column;
}

.track-list-container {
    min-height: 100px;
    overflow: scroll;
}

.track-list-header {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70%;
    min-height: 3.4rem;
    margin-left: 30%;
    margin-bottom: 2rem;
    background-color: #77BD75;
    border-radius: 5px;
    font-weight: 800;
    font-size: 1.6rem;
    padding: 0.2rem 0.2rem 0.2rem 0.2rem
}

.track-list-item {
    width: 100%;
    padding: 0.5rem 2rem 0.5rem 0rem;
    border-bottom: 0.1rem solid #8b8b8b;
}

.track-list-text {
    color: #a5b98b;
    font-weight: 600;
    font-size: 1.2rem;
}

.track-list-subtext {
    color: #77BD75;
    font-weight: 600;
    font-size: 0.8rem;
}

.track-list-icon {
    height: 2rem;
    width: 2rem;
    margin-right: 1rem;
    margin-left: 1rem;
    border-radius: 10%;
}

.track-list-inline-info-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: scroll;
    white-space: nowrap;
}

@media only screen and (max-width: 768px) {

    #track-list-root {
        width: 100%;
    }

    .track-list-header {
        font-size: 1.4rem;
        width: 100%;
        margin-left: 0;
        margin-bottom: 1rem;
    }
    
    .track-list-container {
        margin-bottom: 2rem;
    }

}

@media only screen and (max-width: 400px) {

    .track-list-header {
        font-size: 1rem;
        min-height: 2rem;
    }

} 
#feature-chart-root {
    width: 100%;
    margin-top: 4rem;
    margin-bottom: 4rem;
}

#feature-chart {
    width: 100%;
    height: 100%;
}
#playlist-root {
    display: flex;
    flex-direction: column;
    padding: 3rem 8% 3rem 8%;
}

#playlist-title {
    font-size: 1rem;
    font-weight: 800;
    margin-bottom: 1rem;
    text-align: left;
}

#playlist-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 8%;
    gap: 8%;
}

#playlist-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: left;
    text-align: left;
    align-items: flex-start;
    margin-bottom: 2rem;
}

.playlist-img {
    border-radius: 2%;
    width: 15rem;
    height: 15rem;
    background-size: 15rem 15rem !important;
    border: 0.1rem solid white;
    overflow: auto;
}

@media only screen and (max-width: 768px) {

    .playlist-expanded-img {
        width: 10rem;
        height: 10rem;
        background-size: 10rem 10rem !important;
    }

    #playlist-expanded-info {
        flex-direction: column;
    }

    #playlist-expanded-header {
        margin-bottom: 0rem;
    }

} 


.anticon-close svg, .anticon-check svg {
    font-size: 12px;
}

#recs-refresh-button {
    background-color: #77BD75;
    color: whitesmoke;
    border: none;
    width: 120px;
    align-self: center;
}

#recs-item-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 3rem;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    justify-content: center;
    align-items:flex-start;
}

.recs-refresh {
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-right: 2rem;
    margin-top: 4rem;
    margin-bottom: 3rem;
    margin-left: 2rem;
}

.recs-feature {
    display: flex;
    flex-direction: row;
    text-align: left;
    align-items: center;
    margin-top: 2rem;
}

.queue-parent {
    height: 12rem;
    width: 12rem;
}

.queue-button {
    height: 12rem;
    width: 12rem;
    border: none;
    border-radius: 5%;
    margin-bottom: 0.5rem;
    cursor:pointer;
    background-size: 12rem !important;
}

.queue-indicator {
    width: 100%;
    height: 100%;
    font-weight: 500;
    font-size: 7rem;
    display: none;
    margin: auto;
    color: white;
}

.queue-button:hover {
    opacity: 75%;
}

.queue-button:hover .queue-indicator, .queue-indicator:hover  {
    display: block;
}

.recs-feature-slider {
    width: 50%;
    color: #768761;
}

.recs-feature-label {
    flex-grow: 1;
    font-weight: 800;
    font-size: 1.6rem;
    color: #77BD75;
}

.ant-slider-with-marks {
    margin-bottom: 0
}

.ant-slider-mark-text {
    color: #A1B592;
    font-weight: 600;
}

.ant-slider-mark-text-active {
    color: white;
    font-weight: 600;
}

.ant-slider-dot {
    background-color: #768761;
    border: none;
}

@media only screen and (max-width: 768px) {
    
    .recs-feature-label {
        flex-grow: 1;
        font-weight: 800;
        font-size: 1rem;
        color: #77BD75;
    }

    .recs-feature {
        flex-direction: column;
        
    }

    .recs-refresh {
        flex-direction: column;
        align-items: center;
        margin-left: 0.2rem;
        margin-right: 0.2rem;
        justify-content: center;
        text-align: center;
    }

    #recs-refresh-button {
        background-color: #77BD75;
        color: whitesmoke;
        border: none;
        width: 120px;
        align-self: center;
    }

} 

